<template>
  <div class="amap-wrapper">
    <el-amap class="amap-box" :vid="'amap-vue'" :center="mapCenter" :zoom="zoom">
      <el-amap-marker
        v-for="(marker, index) in markers"
        :position="marker.position"
        :vid="index"
        :key="'p_' + index"
      ></el-amap-marker>
    </el-amap>
  </div>
</template>

<script>
//109.4326777, 24.326970499999998;
export default {
  name: "ShowMap",
  props: {
    location: {
      type: Object,
    },
  },
  watch: {
    location: {
      immediate: true, // 这句重要
      handler(val) {
        if (val) {
          this.markers = [val];
          this.points = [val];
          this.mapCenter = [val.lng, val.lat];
          this.zoom = 18;
        }
      },
    },
  },
  data() {
    return {
      points: [],
      markers: [],
      mapCenter: [],
    };
  },
  methods: {},
};
</script>

<style scoped>
.amap-wrapper {
  width: 100%;
  height: 500px;
  position: relative;
}
.search-box {
  margin: 10px;
  right: 0px;
  position: absolute;
}
</style>
